.feedback-form {
  max-width: 480px;
  background-color: #fff;
  border: 1px solid rgba(0, 0, 0, .12);
  border-radius: 4px;
  flex-direction: column;
  gap: 20px;
  padding: 12px;
  display: flex;
}

.feedback-form label {
  flex-direction: column;
  display: flex;
}

.feedback-form button {
  font: inherit;
  cursor: pointer;
  padding: 8px 12px;
}

input, textarea {
  font: inherit;
  border: 1px solid rgba(33, 33, 33, .2);
  padding: 8px;
}

input:focus, textarea:focus {
  box-shadow: 0 4px 4px rgba(0, 0, 0, .15);
}

textarea {
  resize: none;
}

button {
  color: #fff;
  background: #51a2ff;
  border: none;
  transition: background .4s cubic-bezier(.4, 0, .2, 1), color .4s cubic-bezier(.4, 0, .2, 1);
}

button:hover {
  color: #ff0;
  background: #2e2eff;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .15);
}

